import React from "react";
import tokyo from "../assets/images/tokyo.jpg";
import "../App.css";

import { Link } from "react-router-dom";

import SimpleBottomNavigation from "../component/BottomNavication";

import Button from "@mui/material/Button";
import SendIcon from "@mui/icons-material/Send";
import { useNavigate } from "react-router-dom";
import Top from "../component/Top";

function Home() {
  const navigate = useNavigate();

  const onclick = (day: number) => {
    switch (day) {
      case 1:
        navigate("/FirstDayMain");
        break;
      case 2:
        navigate("/SecondDayMain");
        break;
      case 3:
        navigate("/ThirdDayMain");
        break;
    }
  };
  return (
    <>
      <div className="App">
      <Top title="Home"></Top>
        <header className="App-header">
          <img src={tokyo} className="App-logo" alt="logo" />
          <p>  .</p>
          <p>
            <Button
              variant="contained"
              endIcon={<SendIcon />}
              onClick={() => {
                onclick(1);
              }}
            >
              1
            </Button>
          </p>

          <p>
            <Button
              variant="contained"
              endIcon={<SendIcon />}
              onClick={() => {
                onclick(2);
              }}
            >
              2
            </Button>
          </p>
          <p>
            <Button
              variant="contained"
              endIcon={<SendIcon />}
              onClick={() => {
                onclick(3);
              }}
            >
              3
            </Button>
          </p>
        </header>
        <SimpleBottomNavigation></SimpleBottomNavigation>
      </div>
    </>
  );
}

export default Home;
