import React from "react";
import "../App.css";

import { Link } from "react-router-dom";
import SimpleBottomNavigation from "../component/BottomNavication";
import MediaCard from "../component/MediaCard";
import Top from "../component/Top";

import data from "../data/Second.json"

function SecondDayMain() {
  const list = data.list;
  return (
    <div className="App">
        <Top title={data.title}></Top>
      <header className="App-header">
        {list.map((value) => {
          return (
            <MediaCard
              title={value.title}
              desc={value.desc}
              src={value.src}
              href={value.href}
            />
          );
        })}
      
        <br />
        <br />
        <br />
      </header>

      <SimpleBottomNavigation></SimpleBottomNavigation>
    </div>
  );
}

export default SecondDayMain;
